:root {
  --font-body: "Space Grotesk", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --bg-body: hsl(0, 0%, 8%);
  --bg-body2: #242424;
  --accent: hsl(153, 71%, 59%);
  --text1: hsl(0, 0%, 100%);
  --text2: hsl(0, 0%, 85%);
  --invalid: hsl(7, 100%, 68%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

html,
body {
  overflow-x: hidden;
}

img,
svg {
  display: block;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
input,
textarea,
button {
  font-family: "Space Grotesk", sans-serif;
}

body {
  margin: 0;
  background-color: var(--bg-body);
  font-size: var(--fs-18);
  color: var(--text1);
  line-height: 1.56;
}

.bg-less-dark {
  background-color: var(--bg-body2);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

a.underline,
button {
  display: inline-block;
  padding-bottom: 0.625rem;
  font-size: 1rem;
  line-height: 1.625;
  letter-spacing: 0.143em;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--text1);
  text-decoration: none;
  background-image: linear-gradient(to right, var(--accent) 75%, var(--accent) 75%);
  background-position: 0 2.1em;
  background-repeat: repeat-x;
  background-size: 8px 2px;
}

h1,
h2,
h3,
p {
  margin-block-start: 0;
}

h1,
h2,
h3 {
  line-height: 1;
}

a:focus-visible,
input:focus-visible,
textarea:focus-visible {
  appearance: none;
  border: none;
  outline: none;
}

.xl {
  font-size: 2.5rem;
  font-size: clamp(2.5rem, 0.7rem + 7.68vw, 5.5rem);
  letter-spacing: -0.028em;
  line-height: 1.1;
}

h1 > span {
  background-image: linear-gradient(to right, var(--accent) 75%, var(--accent) 75%);
  background-position: 0 1.18em;
  background-repeat: repeat-x;
  background-size: 8px 4px;
}

.bottom-border {
  border-bottom: 1px solid var(--text2) !important;
}

p {
  font-size: 1rem;
  font-size: clamp(1rem, 0.79rem + 0.89vw, 1.125rem);
  line-height: 1.5;
  color: var(--text2);
}

.wrapper {
  width: calc(100% - 2rem);
  max-width: 69.375rem;
  margin-inline: auto;
}
@media (min-width: 37.56em) {
  .wrapper {
    width: calc(100% - 3.75rem);
  }
}

.header {
  position: relative;
  z-index: 1;
  margin-block-start: 1.25rem;
}
@media (min-width: 37.56em) {
  .header {
    margin-block-start: 1.875rem;
  }
}
@media (min-width: 62.5625em) {
  .header {
    margin-block-start: 2.5rem;
    margin-block-end: 7.9375rem;
  }
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 1.875rem;
}
@media (max-width: 37.5em) {
  .header__nav {
    display: flex;
    justify-content: center;
    text-align: center;
    flex-wrap: wrap;
    gap: 25px;
  }
  .header__nav .header__home {
    flex: 1 0 100%;
  }
  .header__nav .header__socials {
    gap: 0 25px;
  }
}
.header__home {
  display: inline-block;
  text-decoration: none;
  color: var(--text1);
  font-size: 1.5rem;
  font-size: clamp( 1.5rem, 1.0229007633587786rem + 2.035623409669211vw, 2rem );
  line-height: 1;
  transition: all 250ms ease-in-out;
}
.header__home:hover {
  color: var(--accent);
  transform: scale(1.02);
}
.header__socials {
  display: flex;
  justify-content: center;
  gap: 0px 32px;
}
.header__socials > a:hover > svg > path {
  fill: var(--accent);
}
.header__socials > a > svg > path {
  transition: all 250ms ease-in-out;
}
.header__socials > a > svg {
  display: block;
}

