    gap: 60px 24px;
  }
}
.projects__headline {
  grid-column: 1/2;
  margin-block-end: 0;
}
.projects__contact {
  justify-self: end;
  align-self: end;
  grid-column: 2/3;
}
.projects__item {
  grid-column: 1/span 2;
  /* Tablet */
}
@media (min-width: 37.56em) {
