}
.projects__name {
  font-size: 1.5rem;
  line-height: 1.3;
  font-weight: 700;
  text-transform: uppercase;
  margin-block-end: 7px;
}
.projects__tags {
  display: flex;
  gap: 12px;
  margin-block-end: 20px;
  text-transform: uppercase;
}
.projects__links {
  display: flex;
  justify-content: flex-start;
  gap: 20px;
}

/*To reduce the scss file i combined these in one media query for desktop*/
@media (min-width: 62.5em) {
  .projects__grid {
    gap: 70px 30px;
  }
  .projects__item {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, auto);
  }
  .projects__item:hover .projects__picture::after {
    opacity: 0.5;
  }
  .projects__item:hover .projects__links {
    opacity: 1;
  }
  .projects__item:focus-within .projects__links {
    opacity: 1;
  }
  .projects__item:focus-within .projects__picture::after {
    opacity: 0.5;
  }
  .projects__picture {
    position: relative;
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .projects__picture::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    background-color: black;
    opacity: 0;
    transition: opacity 150ms ease-in-out;
  }
  .projects__image {
    width: 100%;
    height: auto;
  }
  .projects__links {
    grid-column: 1/2;
    grid-row: 1/2;
    justify-self: center;
    align-self: center;
    z-index: 1;
    opacity: 0;
    transition: opacity 400ms ease-in-out;
  }
  .projects__links:hover ~ .projects__picture::after {
    opacity: 0.5;
  }
  .projects__links .projects__tags {
    margin-block-end: 0;
  }
}
