.contact__control {
  position: relative;
}
.contact__control > input, .contact__control > textarea {
  resize: none;
  border: none;
  outline: none;
  background-color: transparent;
  border-bottom: 1px solid var(--text2);
  width: 100%;
  color: var(--text1);
  font-size: 1rem;
  line-height: 1.625;
  padding-block: 16px;
  padding-inline: 24px;
}
.contact__control > textarea {
  display: block;
}
.contact__control > button {
  transition: all 250ms ease-in-out;
  border: none;
  background-color: transparent;
}
.contact__control > button:hover {
  color: var(--accent);
}
.contact__control.align-right {
  display: flex;
  justify-content: flex-end;
}
.contact__control > img {
  display: none;
  position: absolute;
  transition: all 1s ease-in-out;
}
.contact__control > img#name_invalid-icon {
  top: 50%;
  right: 0;
  translate: -50% -50%;
}
.contact__control > img#email_invalid-icon {
  top: 50%;
  right: 0;
  translate: -50% -50%;
}
.contact__control > img#message_invalid-icon {
  top: 70%;
  right: 0;
  translate: -50% -50%;
}
.contact__control > p {
  transition: all 1s ease-in-out;
  display: none;
  font-size: 10px;
  color: var(--invalid);
  position: absolute;
  right: 0;
}
.contact__control > p#name_invalid {
  top: 105%;
}
.contact__control > p#email_invalid {
  top: 105%;
}
.contact__control > p#message_invalid {
  top: 105%;
}
.contact__rings {
  position: absolute;
  left: 0;
  translate: -70%;
  bottom: 16%;
}

